import "./App.css";
import Code from "./components/Code";
import Controls from "./components/Controls";
import Canvas from "./components/Canvas";
import { useGlobalContext } from "./context";

function App() {
  const {
    level: { hasCode },
    levelNum,
    toNextLevel,
    toPastLevel,
  } = useGlobalContext();
  return (
    <main>
      {levelNum > 0 && (
        <div className="arrow left" onClick={toPastLevel}>
          <img src="chevron.png" alt="" />
        </div>
      )}
      {levelNum < 11 && (
        <div className="arrow right" onClick={toNextLevel}>
          <img src="chevron.png" alt="" />
        </div>
      )}
      <div className="center">
        {hasCode && (
          <div className="left">
            <Code />
          </div>
        )}

        <div className="right">
          <Controls />
          <Canvas />
        </div>
      </div>
    </main>
  );
}

export default App;
