import React from "react";
import { useGlobalContext } from "../context";

const Canvas = () => {
  const {
    level,
    nextLevel,
    showModal,
    scan,
    fastChange,
    levelsCrossed,
    message,
    levelNum,
    restartLevel,
  } = useGlobalContext();

  const convertToPixels = (position) => {
    return { left: 35 + position[0] * 71, top: 37 + position[1] * 64 };
  };

  const converToDegrees = (heading) => {
    return heading * 90;
  };

  const isGoal = (index, klass = false, field) => {
    var arr = index
      .toString()
      .split("")
      .map((num) => parseInt(num))
      .reverse();
    if (arr.length === 1) {
      arr = [...arr, 0];
    }

    var indices = [];
    for (let index = 0; index < level.goal.length; index++) {
      const element = level.goal[index];

      if (element[0] === arr[0] && element[1] === arr[1]) {
        if (klass) {
          return "goal";
        } else if (level.goal.length > 1) {
          indices.push(index + 1 + levelsCrossed);
        }
      }
    }

    if (indices.length === 0) {
      if (field !== "goal") {
        return field;
      } else {
        return null;
      }
    }
    return indices.join(", ");
  };

  return (
    <section className="canvas">
      <div className="sky"></div>
      <div className="grass"></div>
      <div className="ground">
        <span
          className={`tesla ${scan}`}
          style={{
            ...convertToPixels(level.position),
            transform: `rotate(${converToDegrees(level.heading)}deg)`,
          }}
        >
          ^
        </span>
        {level.hasOwnProperty("scanned")
          ? level.scanned.flat().map((field, index) => (
              <div key={index} className={`${field}`}>
                {field === "house" && <img src="./house.png" alt="" />}
                {field === "lake" && <img src="./lake.png" alt="" />}
                {field === "character" && <img src="./character.png" alt="" />}
              </div>
            ))
          : level.layout.flat().map((field, index) => (
              <div key={index} className={`${isGoal(index, true, field)}`}>
                {field === "house" && <img src="./house.png" alt="" />}
                {field === "lake" && <img src="./lake.png" alt="" />}
                {field === "character" && <img src="./character.png" alt="" />}
                {isGoal(index)}
              </div>
            ))}
      </div>
      <div
        className="modal"
        style={{ transform: `translateX(${showModal * 100}%)` }}
        onClick={levelNum < 11 ? nextLevel : null}
      >
        <h1>
          {message.split("\n").map((str, index) => (
            <p key={index}>{str}</p>
          ))}
        </h1>
        {levelNum < 11 ? (
          <p>Klikni pre pokračovanie do ďalšieho levelu.</p>
        ) : (
          <p>Ďalšie levely prídu časom.</p>
        )}
      </div>
      {level.hasOwnProperty("fast") && (
        <div className="lightning" onClick={fastChange}>
          {level.fast ? (
            <img src="./lightningOn.png" alt="" />
          ) : (
            <img src="./lightningOff.png" alt="" />
          )}
        </div>
      )}
      <div className="restart" onClick={restartLevel}>
        <img src="./restart.png" alt="" />
      </div>
    </section>
  );
};

export default Canvas;
