import React from "react";
import { useGlobalContext } from "../context";

const Control = ({ type }) => {
  const funktion = useGlobalContext()[type];
  const {
    shake,
    level: { hasCode },
  } = useGlobalContext();
  return (
    <div
      className={`${type} ${type === "forward" ? shake : ""}`}
      onClick={() => funktion(hasCode)}
      style={{ backgroundImage: `${type}.png` }}
    >
      <img src={`./${type}.png`} alt="" />
    </div>
  );
};

export default Control;
