import { levels } from "./levels";

const reducer = (state, action) => {
  if (action.type === "MOVE_UP") {
    return {
      ...state,
      level: {
        ...state.level,
        position: [state.level.position[0], state.level.position[1] - 1],
      },
      started: true,
    };
  } else if (action.type === "MOVE_RIGHT") {
    return {
      ...state,
      level: {
        ...state.level,
        position: [state.level.position[0] + 1, state.level.position[1]],
      },
      started: true,
    };
  } else if (action.type === "MOVE_DOWN") {
    return {
      ...state,
      level: {
        ...state.level,
        position: [state.level.position[0], state.level.position[1] + 1],
      },
      started: true,
    };
  } else if (action.type === "MOVE_LEFT") {
    return {
      ...state,
      level: {
        ...state.level,
        position: [state.level.position[0] - 1, state.level.position[1]],
      },
      started: true,
    };
  } else if (action.type === "SHAKE") {
    return {
      ...state,
      shake: "shake",
    };
  } else if (action.type === "STOP_SHAKING") {
    return {
      ...state,
      shake: "",
    };
  } else if (action.type === "TURN_RIGHT") {
    const newHeading = state.level.heading + 1;

    return {
      ...state,
      level: { ...state.level, heading: newHeading },
      started: true,
    };
  } else if (action.type === "TURN_LEFT") {
    const newHeading = state.level.heading - 1;

    return {
      ...state,
      level: { ...state.level, heading: newHeading },
      started: true,
    };
  } else if (action.type === "START_SCANNING") {
    return {
      ...state,
      scan: "scan",
    };
  } else if (action.type === "CHANGE_COLOR") {
    return {
      ...state,
      scan: `scan ${action.payload}`,
    };
  } else if (action.type === "STOP_SCANNING") {
    return {
      ...state,
      scan: "",
    };
  } else if (action.type === "CHANGE_BLOCK") {
    const scannedLayout = [
      [...state.level.scanned[0]],
      [...state.level.scanned[1]],
      [...state.level.scanned[2]],
      [...state.level.scanned[3]],
      [...state.level.scanned[4]],
    ];

    const position = action.payload.position;
    scannedLayout[position[1]][position[0]] = action.payload.block;

    return {
      ...state,
      level: {
        ...state.level,
        scanned: scannedLayout,
      },
    };
  } else if (action.type === "CHANGE_FAST") {
    return {
      ...state,
      level: {
        ...state.level,
        fast: !state.level.fast,
      },
    };
  } else if (action.type === "GOAL_REACHED") {
    // const layout = [
    //   [...state.level.layout[0]],
    //   [...state.level.layout[1]],
    //   [...state.level.layout[2]],
    //   [...state.level.layout[3]],
    //   [...state.level.layout[4]],
    // ];

    // layout

    // console.log(state.level.goal.slice(1));
    return {
      ...state,
      levelsCrossed: state.levelsCrossed + 1,
      level: {
        ...state.level,
        goal: state.level.goal.slice(1),
      },
    };
  } else if (action.type === "LEVEL_DONE") {
    return { ...state, done: true };
  } else if (action.type === "OPEN_MODAL") {
    return { ...state, showModal: 0 };
  } else if (action.type === "NEXT_LEVEL") {
    return {
      ...state,
      levelsCrossed: 0,
      level: levels[state.levelNum + 1],
      showModal: 1,
      levelNum: state.levelNum + 1,
      celebrate: "",
      done: false,
    };
  } else if (action.type === "CHANGE_MESSAGE") {
    return {
      ...state,
      message: state.level.message,
    };
  } else if (action.type === "RESTART_LEVEL") {
    return {
      ...state,
      level: levels[state.levelNum],
    };
  } else if (action.type === "TO_NEXT_LEVEL") {
    return {
      ...state,
      level: levels[state.levelNum + 1],
      levelNum: state.levelNum + 1,
    };
  } else if (action.type === "TO_PAST_LEVEL") {
    return {
      ...state,
      level: levels[state.levelNum - 1],
      levelNum: state.levelNum - 1,
    };
  } else {
    throw new Error("Reducer action missing");
  }
};

export default reducer;
