import React from "react";
import { useGlobalContext } from "../context";

const Code = () => {
  const {
    code,
    runCode,
    level: { hasCode },
  } = useGlobalContext();

  if (hasCode) {
    return (
      <section className="code">
        <h1>Kód píš tu:</h1>
        <textarea ref={code} />
        <button onClick={runCode}>Spustiť</button>
      </section>
    );
  }
  return <section className="code">this doesnt have code</section>;
};

export default Code;
