import React, { useContext, useReducer, useRef } from "react";
import reducer from "./reducer";
import { levels } from "./levels";

const AppContext = React.createContext();

const initialState = {
  level: levels[0],
  shake: "",
  done: false,
  levelsCrossed: 0,
  showModal: 1,
  levelNum: 0,
  scan: "",
  message: "Skvelá práca!",
};

const AppProvider = ({ children }) => {
  const [state, dispatch] = useReducer(reducer, initialState);
  const code = useRef(null);

  const block = (unpassable, position) => {
    var thisBlock = state.level.layout[position[1]][position[0]];
    if (state.level.hasOwnProperty("scanned")) {
      thisBlock = state.level.scanned[position[1]][position[0]];
    }

    if (unpassable.includes(thisBlock)) {
      return true;
    }
    return false;
  };

  const runCode = async () => {
    const lines = code.current.value.split("\n").map((line) => line.split(" "));
    const stepTime = 150;
    var numOfFunc = 0;
    var heading = state.level.heading % 4;
    const position = [...state.level.position];

    var unpassable = state.level.layout.flat().filter((n) => n !== "goal" && n);
    if (state.level.hasOwnProperty("scanned")) {
      unpassable = [...unpassable, "not"];
    }

    for (let yndex = 0; yndex < lines.length; yndex++) {
      const line = lines[yndex];

      if (line.length === 1) {
        if (line[0] === "dopredu") {
          numOfFunc++;

          if (heading === 0) {
            position[1] = position[1] - 1;
            if (position[1] < 0 || block(unpassable, position)) {
              position[1] += 1;
              setTimeout(() => {
                shake();
              }, numOfFunc * stepTime);
            } else {
              setTimeout(() => {
                dispatch({ type: "MOVE_UP" });
              }, numOfFunc * stepTime);
            }
          } else if (heading === 1 || heading === -3) {
            position[0] = position[0] + 1;
            if (position[0] > 9 || block(unpassable, position)) {
              position[0] -= 1;
              setTimeout(() => {
                shake();
              }, numOfFunc * stepTime);
            } else {
              setTimeout(() => {
                dispatch({ type: "MOVE_RIGHT" });
              }, numOfFunc * stepTime);
            }
          } else if (heading === 2 || heading === -2) {
            position[1] = position[1] + 1;
            if (position[1] > 4 || block(unpassable, position)) {
              position[1] -= 1;
              setTimeout(() => {
                shake();
              }, numOfFunc * stepTime);
            } else {
              setTimeout(() => {
                dispatch({ type: "MOVE_DOWN" });
              }, numOfFunc * stepTime);
            }
          } else if (heading === 3 || heading === -1) {
            position[0] = position[0] - 1;
            if (position[0] < 0 || block(unpassable, position)) {
              position[0] += 1;
              setTimeout(() => {
                shake();
              }, numOfFunc * stepTime);
            } else {
              setTimeout(() => {
                dispatch({ type: "MOVE_LEFT" });
              }, numOfFunc * stepTime);
            }
          }
        } else if (line[0] === "vlavo" || line[0] === "dolava") {
          numOfFunc++;
          heading = (heading - 1) % 4;

          setTimeout(() => {
            left(false);
          }, numOfFunc * stepTime);
        } else if (line[0] === "vpravo" || line[0] === "doprava") {
          numOfFunc++;
          heading = (heading + 1) % 4;

          setTimeout(() => {
            right(false);
          }, numOfFunc * stepTime);
        }
      } else if (line.length === 2) {
        if (line[0] === "dopredu") {
          const times = parseInt(line[1]);
          for (let index = 0; index < times; index++) {
            console.log(position);
            numOfFunc++;
            if (heading === 0) {
              position[1] = position[1] - 1;
              if (position[1] < 0 || block(unpassable, position)) {
                position[1] += 1;
                setTimeout(() => {
                  shake();
                }, numOfFunc * stepTime);
              } else {
                setTimeout(() => {
                  dispatch({ type: "MOVE_UP" });
                }, numOfFunc * stepTime);
              }
            } else if (heading === 1 || heading === -3) {
              position[0] = position[0] + 1;

              if (position[0] > 9 || block(unpassable, position)) {
                position[0] -= 1;
                setTimeout(() => {
                  shake();
                }, numOfFunc * stepTime);
              } else {
                setTimeout(() => {
                  dispatch({ type: "MOVE_RIGHT" });
                }, numOfFunc * stepTime);
              }
            } else if (heading === 2 || heading === -2) {
              position[1] = position[1] + 1;

              if (position[1] > 4 || block(unpassable, position)) {
                position[1] -= 1;
                setTimeout(() => {
                  shake();
                }, numOfFunc * stepTime);
              } else {
                setTimeout(() => {
                  dispatch({ type: "MOVE_DOWN" });
                }, numOfFunc * stepTime);
              }
            } else if (heading === 3 || heading === -1) {
              position[0] = position[0] - 1;

              if (position[0] < 0 || block(unpassable, position)) {
                position[0] += 1;
                setTimeout(() => {
                  shake();
                }, numOfFunc * stepTime);
              } else {
                setTimeout(() => {
                  dispatch({ type: "MOVE_LEFT" });
                }, numOfFunc * stepTime);
              }
            }
          }
        } else if (line[0] === "vlavo" || line[0] === "dolava") {
          const times = parseInt(line[1]);
          for (let index = 0; index < times; index++) {
            heading = (heading - 1) % 4;
            numOfFunc++;
            setTimeout(() => {
              left();
            }, numOfFunc * stepTime);
          }
        } else if (line[0] === "vpravo" || line[0] === "doprava") {
          const times = parseInt(line[1]);
          for (let index = 0; index < times; index++) {
            heading = (heading + 1) % 4;

            numOfFunc++;
            setTimeout(() => {
              right();
            }, numOfFunc * stepTime);
          }
        }
      }
    }

    goalReached(0, 0, position, numOfFunc * stepTime);
  };

  const goalReached = (
    index = 0,
    inc = 0,
    position = [...state.level.position],
    waitTime = 0
  ) => {
    position[index] += inc;
    const goal = state.level.goal;

    console.log("pos", position);
    console.log(goal);

    if (goal.length === 1) {
      if (position[0] === goal[0][0] && position[1] === goal[0][1]) {
        dispatch({ type: "LEVEL_DONE" });
        setTimeout(() => {
          dispatch({ type: "OPEN_MODAL" });
        }, waitTime + 1000);
      }
    } else {
      if (position[0] === goal[0][0] && position[1] === goal[0][1]) {
        setTimeout(() => dispatch({ type: "GOAL_REACHED" }), waitTime + 300);
      }
    }
  };

  const checkBlock = (index, inc, scan = false) => {
    const position = [...state.level.position];
    position[index] += inc;

    var unpassable = state.level.layout.flat().filter((n) => n !== "goal" && n);
    if (state.level.hasOwnProperty("scanned")) {
      unpassable = [...unpassable, "not"];
    }

    try {
      var nextBlock = state.level.layout[position[1]][position[0]];
      if (scan) {
        setTimeout(
          () => {
            dispatch({
              type: "CHANGE_BLOCK",
              payload: { block: nextBlock, position },
            });
          },
          state.level.fast ? 200 : 1000
        );
      } else if (state.level.hasOwnProperty("scanned")) {
        if (state.level.scanned[position[1]][position[0]] === "not") {
          return false;
        }
      }

      if (unpassable.includes(nextBlock)) {
        return false;
      }
      return true;
    } catch (e) {
      throw Error(e);
    }
  };

  const nextLevel = () => {
    dispatch({ type: "NEXT_LEVEL" });
    clearCode();
    setTimeout(() => dispatch({ type: "CHANGE_MESSAGE" }), 1500);
  };

  const shake = () => {
    dispatch({ type: "SHAKE" });
    setTimeout(() => {
      dispatch({ type: "STOP_SHAKING" });
    }, 500);
  };

  const forward = (usingCode = false) => {
    if (usingCode) {
      const lines = code.current.value
        .split("\n")
        .map((line) => line.split(" "));

      const lastLine = lines[lines.length - 1];

      if (lastLine[0] === "" && lastLine.length === 1) {
        code.current.value += "dopredu";
      } else if (lastLine[0] === "dopredu" && lastLine.length === 1) {
        code.current.value += " 2";
      } else if (lastLine[0] === "dopredu" && lastLine.length === 2) {
        const num = lastLine[lastLine.length - 1];
        code.current.value = code.current.value.slice(
          0,
          code.current.value.length - 1 - num.length
        );
        code.current.value += ` ${parseInt(num) + 1}`;
      }
    } else {
      if (state.done) {
        return;
      } else {
        const heading = state.level.heading % 4;
        if (heading === 0) {
          if (state.level.position[1] > 0) {
            if (checkBlock(1, -1)) {
              dispatch({ type: "MOVE_UP" });
              goalReached(1, -1);
            } else {
              shake();
            }
          } else {
            shake();
          }
        } else if (heading === 1 || heading === -3) {
          if (state.level.position[0] < 9) {
            if (checkBlock(0, 1)) {
              dispatch({ type: "MOVE_RIGHT" });
              goalReached(0, 1);
            } else {
              shake();
            }
          } else {
            shake();
          }
        } else if (heading === 2 || heading === -2) {
          if (state.level.position[1] < 4) {
            if (checkBlock(1, 1)) {
              dispatch({ type: "MOVE_DOWN" });
              goalReached(1, 1);
            } else {
              shake();
            }
          } else {
            shake();
          }
        } else if (heading === 3 || heading === -1) {
          if (state.level.position[0] > 0) {
            if (checkBlock(0, -1)) {
              dispatch({ type: "MOVE_LEFT" });
              goalReached(0, -1);
            } else {
              shake();
            }
          } else {
            shake();
          }
        }
      }
    }
  };

  const right = (usingCode) => {
    if (usingCode) {
      const lines = code.current.value
        .split("\n")
        .map((line) => line.split(" "));

      const lastLine = lines[lines.length - 1];

      if (lastLine[0] === "" && lastLine.length === 1) {
        code.current.value += "vpravo";
      } else if (lastLine[0] === "vpravo" && lastLine.length === 1) {
        code.current.value += " 2";
      } else if (lastLine[0] === "vpravo" && lastLine.length === 2) {
        const num = lastLine[lastLine.length - 1];
        code.current.value = code.current.value.slice(
          0,
          code.current.value.length - 1 - num.length
        );
        code.current.value += ` ${parseInt(num) + 1}`;
      }
    } else {
      dispatch({ type: "TURN_RIGHT" });
    }
  };

  const left = (usingCode) => {
    if (usingCode) {
      const lines = code.current.value
        .split("\n")
        .map((line) => line.split(" "));

      const lastLine = lines[lines.length - 1];

      if (lastLine[0] === "" && lastLine.length === 1) {
        code.current.value += "vlavo";
      } else if (lastLine[0] === "vlavo" && lastLine.length === 1) {
        code.current.value += " 2";
      } else if (lastLine[0] === "vlavo" && lastLine.length === 2) {
        const num = lastLine[lastLine.length - 1];
        code.current.value = code.current.value.slice(
          0,
          code.current.value.length - 1 - num.length
        );
        code.current.value += ` ${parseInt(num) + 1}`;
      }
    } else {
      dispatch({ type: "TURN_LEFT" });
    }
  };

  const scanner = () => {
    dispatch({ type: "START_SCANNING" });

    setTimeout(
      () => {
        const heading = state.level.heading % 4;
        if (heading === 0) {
          if (state.level.position[1] > 0) {
            if (checkBlock(1, -1, true)) {
              dispatch({ type: "CHANGE_COLOR", payload: "green" });
            } else {
              dispatch({ type: "CHANGE_COLOR", payload: "red" });
            }
          } else {
            dispatch({ type: "CHANGE_COLOR", payload: "red" });
          }
        } else if (heading === 1 || heading === -3) {
          if (state.level.position[0] < 9) {
            if (checkBlock(0, 1, true)) {
              dispatch({ type: "CHANGE_COLOR", payload: "green" });
            } else {
              dispatch({ type: "CHANGE_COLOR", payload: "red" });
            }
          } else {
            dispatch({ type: "CHANGE_COLOR", payload: "red" });
          }
        } else if (heading === 2 || heading === -2) {
          if (state.level.position[1] < 4) {
            if (checkBlock(1, 1, true)) {
              dispatch({ type: "CHANGE_COLOR", payload: "green" });
            } else {
              dispatch({ type: "CHANGE_COLOR", payload: "red" });
            }
          } else {
            dispatch({ type: "CHANGE_COLOR", payload: "red" });
          }
        } else if (heading === 3 || heading === -1) {
          if (state.level.position[0] > 0) {
            if (checkBlock(0, -1, true)) {
              dispatch({ type: "CHANGE_COLOR", payload: "green" });
            } else {
              dispatch({ type: "CHANGE_COLOR", payload: "red" });
            }
          } else {
            dispatch({ type: "CHANGE_COLOR", payload: "red" });
          }
        }
      },
      state.level.fast ? 500 : 1500
    );
    setTimeout(
      () => {
        dispatch({ type: "STOP_SCANNING" });
      },
      state.level.fast ? 1000 : 2500
    );
  };

  const fastChange = () => {
    dispatch({ type: "CHANGE_FAST" });
  };

  const restartLevel = () => {
    dispatch({ type: "RESTART_LEVEL" });
  };

  const toNextLevel = () => {
    dispatch({ type: "TO_NEXT_LEVEL" });
    clearCode();
  };

  const toPastLevel = () => {
    dispatch({ type: "TO_PAST_LEVEL" });
    clearCode();
  };

  const clearCode = () => {
    console.log(code);
    if (code.current !== null) {
      code.current.value = "";
    }
  };

  return (
    <AppContext.Provider
      value={{
        ...state,
        forward,
        right,
        left,
        scanner,
        goalReached,
        nextLevel,
        fastChange,
        runCode,
        restartLevel,
        toNextLevel,
        toPastLevel,
        code,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export const useGlobalContext = () => {
  return useContext(AppContext);
};

export { AppProvider, AppContext };
