import React from "react";
import { useGlobalContext } from "../context";
import Control from "./Control";

const Controls = () => {
  const {
    level: { show },
  } = useGlobalContext();
  return (
    <section className="controls">
      {show.map((control, index) => (
        <Control type={control} key={index} />
      ))}
    </section>
  );
};

export default Controls;
